import { useState, useEffect } from "react";
import "./styles.css";
import { names, emojis } from "./names";

const getRandomInt = (number) => Math.floor(number * Math.random());

export default function App() {
  const [nameIndex, setNameIndex] = useState(getRandomInt(names?.length));
  const [emojiIndex, setEmojiIndex] = useState(getRandomInt(emojis?.length));

  useEffect(() => {
    const interval = setInterval(() => {
      setEmojiIndex(getRandomInt(emojis?.length));
      setNameIndex(getRandomInt(emojis?.length));
    }, 4000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="App">
      <h1 className="h1">
        {emojis?.[emojiIndex]} {names?.[nameIndex]} {emojis?.[emojiIndex]}
      </h1>
    </div>
  );
}
